function normalize(input) {
  return input.sort().join(' ').replace(/\s+/g, '').toLowerCase()
}

function isCorrect(answer, response) {
  const normalAnswer = normalize(answer)
  const normalResponse = normalize(response)
  return normalAnswer === normalResponse
}

function calcTotalScore(questions, answers) {
  let score = 0

  questions.forEach((question, index) => {
    if (isCorrect(question.answer, answers[index])) score+=1
  })

  return score
}

const FinalScreen = ({ questions, answers }) => {
  const score = calcTotalScore(questions, answers) 

  return (
    <div>
      <h4>Total score: {score} of {questions.length} ({Math.round((score / questions.length) *100)}%)</h4>

      <hr /> 
      {questions.map((question, index) => {
        return (
          <div key={index}>
            <p>{index + 1}. <strong>{isCorrect(question.answer, answers[index]) ? "CORRECT" : "INCORRECT"}</strong> - {question.text}</p>
            
            <p>Your answer: {answers[index].join(', ')}</p>
            <p>Correct answer: {question.answer.join(', ')}</p>
            <hr />
          </div>
        )
      })}
    </div>
  )
}

export default FinalScreen